.is-hidden {
  display: none;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  display: flex;
}

.info {
  flex-wrap: wrap;
  justify-content: space-around;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.photo-card {
  width: calc(25% - 11.25px);
  border-radius: 5px;
}

.photo-card img {
  height: 200px;
  width: 100%;
  object-fit: cover;
  margin: 0 auto;
  display: block;
}

